}
.circle-1 {
  background: rgba(210, 175, 255, 0.25);
  width: 500px;
  height: 500px;
  top: -100px;
  right: -150px;
  animation: pulse 8s infinite alternate ease-in-out;
}
.circle-2 {
  background: rgba(0, 201, 255, 0.15);
  width: 600px;
  height: 600px;
  bottom: -200px;
  left: -200px;
  animation: pulse 12s infinite alternate-reverse ease-in-out;
}
.features {
  max-width: 1200px;
  margin: 0 auto;
  padding: 8rem 2rem;
}
.section-title {
  text-align: center;
  font-size: 2.8rem;
  margin-bottom: 4rem;
  position: relative;
  font-weight: 800;
  letter-spacing: -0.5px;
}
.section-title::after {
  content: "";
  display: block;
  width: 100px;
  height: 6px;
  background: var(--gradient-primary);
  margin: 1.5rem auto 0;
  border-radius: 3px;
