  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2.5rem;
  margin-top: 3rem;
}
.tone-card {
  background: rgba(255, 255, 255, 0.03);
  border-radius: 16px;
  padding: 2.5rem 2rem;
  text-align: center;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  cursor: pointer;
  border: 1px solid rgba(255, 255, 255, 0.05);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  position: relative;
  overflow: hidden;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.tone-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(210, 175, 255, 0.05) 0%, rgba(0, 0, 0, 0) 100%);
  z-index: 0;
}
.tone-card:hover {
  transform: translateY(-15px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
  border-color: var(--primary);
  background: rgba(255, 255, 255, 0.07);
}
.tone-card h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: var(--primary);
  font-weight: 700;
  position: relative;
  z-index: 1;
}
.tone-card p {
  color: var(--light-accent);
  line-height: 1.7;
  position: relative;
  z-index: 1;
}
.tone-icon {
  font-size: 3rem;
  margin-bottom: 1.5rem;
  transform: rotate(-5deg);
  display: inline-block;
  filter: drop-shadow(0 5px 15px rgba(0, 0, 0, 0.3));
  transition: all 0.3s;
  position: relative;
  z-index: 1;
}
.tone-card:hover .tone-icon {
  transform: rotate(0deg) scale(1.2);
}
.how-it-works {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.1));
  padding: 8rem 2rem;
  position: relative;
  overflow: hidden;
}
.how-it-works::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23ffffff' fill-opacity='0.05'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  z-index: 0;
  pointer-events: none;
}
.steps {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}
.step {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background: rgba(0, 0, 0, 0.2);
  padding: 3rem 2rem;
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}
.step:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
  border-color: rgba(255, 255, 255, 0.1);
}
.step::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(210, 175, 255, 0.03) 0%, rgba(0, 0, 0, 0) 70%);
  z-index: -1;
}
.step-number {
  width: 60px;
  height: 60px;
  background: var(--gradient-primary);
  color: var(--light);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.8rem;
  font-weight: 800;
  margin-bottom: 2rem;
  box-shadow: 0 10px 20px rgba(144, 102, 199, 0.3);
}
.step h3 {
  font-size: 1.5rem;
  margin-bottom: 1.2rem;
  font-weight: 700;
  color: var(--light);
}
.step p {
  color: var(--light-accent);
  line-height: 1.7;
}
.gallery {
  max-width: 1200px;
  margin: 0 auto;
  padding: 8rem 2rem;
}
.examples {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2.5rem;
  margin-top: 4rem;
}
.example-card {
  background-size: cover;
  background-position: center;
  height: 280px;
  border-radius: 16px;
  overflow: hidden;
  position: relative;
  cursor: pointer;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3);
  transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
}
.example-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.5);
}
.example-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.7s cubic-bezier(0.19, 1, 0.22, 1);
  filter: brightness(0.9) saturate(1.2);
}
.example-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0.5) 50%, transparent 100%);
  padding: 2rem;
  transform: translateY(60%);
  transition: transform 0.5s cubic-bezier(0.19, 1, 0.22, 1);
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.example-card:hover .example-img {
  transform: scale(1.1) rotate(1deg);
}
.example-card:hover .example-overlay {
  transform: translateY(0);
}
.example-overlay h4 {
  font-size: 1.4rem;
  margin-bottom: 1rem;
  color: var(--light);
  font-weight: 700;
  transform: translateY(20px);
  opacity: 0;
  transition: all 0.5s ease 0.1s;
}
.example-overlay p {
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.9);
  transform: translateY(20px);
  opacity: 0;
  transition: all 0.5s ease 0.2s;
}
.example-card:hover .example-overlay h4,
.example-card:hover .example-overlay p {
  transform: translateY(0);
  opacity: 1;
}
.testimonials {
  background: rgba(255, 255, 255, 0.02);
  padding: 6rem 2rem;
}
.testimonial-container {
  max-width: 800px;
  margin: 0 auto;
}
@layer components {
  .testimonial-card {
    position: relative;
    margin-bottom: calc(var(--spacing) * 8);
    border-radius: 10px;
    border-style: var(--tw-border-style);
    border-width: 1px;
    border-color: rgba(255,255,255,0.1);
    background-color: rgba(255,255,255,0.05);
    padding: calc(var(--spacing) * 8);
    &::before {
      content: var(--tw-content);
      position: absolute;
    }
    &::before {
      content: var(--tw-content);
      top: -20px;
    }
    &::before {
      content: var(--tw-content);
      left: 20px;
    }
    &::before {
      content: var(--tw-content);
      font-family: var(--font-serif);
    }
    &::before {
      content: var(--tw-content);
      font-size: 5rem;
    }
    &::before {
      content: var(--tw-content);
      --tw-leading: 1;
      line-height: 1;
    }
    &::before {
      content: var(--tw-content);
      color: var(--primary);
    }
    &::before {
      content: var(--tw-content);
      opacity: 30%;
    }
    &::before {
      content: var(--tw-content);
      --tw-content: '\"';
      content: var(--tw-content);
    }
  }
}
.testimonial {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  padding: 2rem;
  margin-bottom: 2rem;
  position: relative;
  border: 1px solid rgba(255, 255, 255, 0.1);
}
.testimonial::before {
  content: '"';
  position: absolute;
  top: -20px;
  left: 20px;
  font-size: 5rem;
  color: var(--primary);
  opacity: 0.3;
  line-height: 1;
}
.testimonial-text {
  font-style: italic;
  margin-bottom: 1.5rem;
  line-height: 1.6;
}
.testimonial-author {
  display: flex;
  align-items: center;
}
.testimonial-author-text {
  margin-left: 10px;
}
.author-avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 1rem;
  object-fit: cover;
}
.author-name {
  font-weight: 700;
}
.author-title {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
}
.cta-section {
  max-width: 1200px;
  margin: 0 auto;
  padding: 6rem 2rem;
  text-align: center;
}
.cta-title {
  font-size: 3rem;
  margin-bottom: 1.5rem;
}
.cta-text {
  font-size: 1.2rem;
  max-width: 600px;
  margin: 0 auto 3rem;
  line-height: 1.6;
}
footer {
  background: rgba(0, 0, 0, 0.3);
  padding: 4rem 2rem 2rem;
  text-align: center;
}
.footer-content {
  max-width: 1200px;
  margin: 0 auto;
}
.footer-links {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2rem;
  margin-bottom: 2rem;
}
.footer-links a {
  color: var(--light);
  text-decoration: none;
  transition: color 0.3s;
}
.footer-links a:hover {
  color: var(--primary);
}
.copyright {
  margin-top: 2rem;
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.9rem;
}
.floating-gif {
  position: absolute;
  width: 150px;
  z-index: 1;
  opacity: 0.7;
  animation: float 8s infinite ease-in-out;
  pointer-events: none;
}
.gif-1 {
  top: 20%;
  right: 10%;
  animation-delay: 0s;
}
.gif-2 {
  bottom: 15%;
  left: 5%;
  animation-delay: 2s;
}
.gif-3 {
  top: 10%;
  left: 15%;
  animation-delay: 4s;
}
.gif-4 {
  top: 65%;
  left: 85%;
  animation-delay: 5s;
}
@keyframes pulse {
  0% {
    opacity: 0.5;
    transform: scale(1);
  }
  100% {
    opacity: 0.8;
    transform: scale(1.1);
  }
}
@keyframes float {
  0% {
    transform: translateY(0) rotate(0deg);
  }
  50% {
    transform: translateY(-15px) rotate(5deg);
  }
  100% {
    transform: translateY(0) rotate(0deg);
  }
}
@keyframes glitch-effect {
  0% {
    clip: rect(24px, 550px, 90px, 0);
    opacity: 0.2;
  }
  20% {
    clip: rect(35px, 550px, 100px, 0);
    opacity: 0.4;
  }
  40% {
    clip: rect(66px, 550px, 84px, 0);
    opacity: 0.6;
  }
  60% {
    clip: rect(74px, 550px, 94px, 0);
    opacity: 0.4;
  }
  80% {
    clip: rect(15px, 550px, 64px, 0);
    opacity: 0.2;
  }
  100% {
    clip: rect(24px, 550px, 90px, 0);
    opacity: 0;
  }
}
@keyframes doorSlam {
  0% {
    transform: skewX(-25deg) translateX(0);
  }
  100% {
    transform: skewX(-25deg) translateX(200%);
  }
}
.glitch-effect {
  position: relative;
  display: inline-block;
}
.glitch-effect::before, .glitch-effect::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.glitch-effect::before {
  color: #B088FF;
  z-index: -1;
  animation: glitch-effect 2s infinite;
  clip: rect(24px, 550px, 90px, 0);
  transform: skew(0.8deg);
}
.glitch-effect::after {
  color: #8F00FF;
  z-index: -2;
  animation: glitch-effect 2s infinite alternate-reverse;
  clip: rect(85px, 550px, 140px, 0);
  transform: skew(-0.8deg);
}
.door-slam-btn {
  position: relative;
  overflow: hidden;
}
.door-slam-btn::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 0;
  background: rgba(255, 255, 255, 0.2);
  transform: skewX(-25deg);
  transition: all 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.door-slam-btn:hover::before {
  width: 150%;
  animation: doorSlam 0.5s forwards;
}
@media (max-width: 768px) {
  h1 {
    font-size: 2.5rem;
  }
  .tagline {
    font-size: 1.2rem;
  }
  .nav-links {
    display: none;
  }
  .tone-cards, .steps, .examples {
    grid-template-columns: 1fr;
  }
  .features, .how-it-works, .gallery, .testimonials, .cta-section {
    padding: 4rem 1.5rem;
  }
  .cta-title {
    font-size: 2rem;
  }
}
